import React from 'react';
import PropTypes from 'prop-types';
import { translate } from '../Translator';

export default class BippoField extends React.Component {

    static propTypes = {
        comp: PropTypes.func,
        editProps: PropTypes.shape({}),             // from JSX
        hidden: PropTypes.bool,                     // whole field is hidden if true
        id: PropTypes.string,                       // 'surname'
        path: PropTypes.arrayOf(PropTypes.string),  // ['testForm','relatives',0,'parents']
        parent: PropTypes.shape({}),                // ref to containing parent section
        required: PropTypes.bool,                   // passed to component - can be overriden in editProps
        showLabel: PropTypes.bool,                  // can be used to hide labels
        stateData: PropTypes.shape({}),             // value, touch
        updateRedux: PropTypes.func,
        validator: PropTypes.func
    };

    onChange = (ev) => {
        const {path, stateData, updateRedux, parent, id} = this.props;
        const newData = {
            ...stateData,
            value: ev.target.value,
            touch: true
        };
        parent.fieldChanged(this.props.getDisp(),id,newData,stateData);
        updateRedux({type: 'SETPATH', key: path, value: newData });
    };

    onStateChange = (st) => {
        const {path, updateRedux} = this.props;
        console.log('onsta',st);
        updateRedux({type: 'SETPATH', key: path, value: st});
    };

    render () {
        if (this.props.hidden) {
            return null;
        }
        const { path, stateData, required, comp, editProps, showLabel = true, validator = defaultValidator } = this.props;
        const value = stateData.value;
        const touch = stateData.touch;
        const id = path.join('-');
        const label = showLabel && translate(id);
        const Compo = comp || InpText;
        const error = touch && validator(value, required, stateData, editProps);

        return (
            <div key={id}>
                <div className={'field'}>
                    {showLabel &&
                        <label htmlFor={id} onClick={this.debug}>{label}</label>
                    }
                    <Compo
                        id={id}
                        onChange={this.onChange}
                        onStateChange={this.onStateChange}
                        required={required}
                        touch={touch}
                        value={value}
                        {...stateData.editProps}
                        {...editProps}
                    />
                    {error &&
                        <span id={id+'-error'}>{error}</span>
                    }
                </div>
            </div>
        );
    }

}

function defaultValidator (value, required) {
    if (!value && required)
        return 'required';
    return null;
}

export function dateValidator (value, required) {
    if (required) {
        if (!value || !value.day || !value.month || !value.year)
            return 'date-required';
    }
    return null;
}

function InpText ({ id, onChange, value, required, maxLength }) {
    return (<input
        type="text" id={id} onChange={onChange} value={value || ''} required={required}
        maxLength={maxLength}
    />);
}

/*
example stateData
    {
    value: '2387288',
    hidden: false,
    touched: true,
    required: false,
    labelId: 'cardRef',
    editProps: {         // passed directly to input component
        maxLength: 11,
        minLength: 4
    }

<Input pattern={alpha} required />
<Input type="tel" required={xxx} />

  PROPS WHICH MATTER TO THE 'FIELD'

    labelId     ie 'idExpiryDate'
    showLabel   (default to true)
    labelValues {}

    id
    path

    value
    touch
    validator   func

  PROPS WHICH MATTER TO THE INPUT

    value
    touch
    optionsId
    required

*/



